@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Poppins', sans-serif;
}

.container {
  max-width: 500px;
  margin: 30px auto;
  overflow: auto;
  min-height: 300px;
  border: none;
  padding: 30px;
  border-radius: 5px;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.btn {
  display: inline-block;
  background: #000;
  color: #fff;
  border: none;
  padding: 10px 20px;
  margin: 5px;
  border-radius: 5px;
  cursor: pointer;
  text-decoration: none;
  font-size: 15px;
  font-family: inherit;
}

.btn:focus {
  outline: none;
}

.btn:active {
  transform: scale(0.98);
}

.btn-block {
  display: block;
  width: 100%;
}

.task {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #f4f4f4;
  margin: 5px;
  min-height: 10vh;
  padding: 10px 20px;
  cursor: pointer;
  word-break: break-word;
 
}



.task-content{
  padding:10px 0px;
  position: absolute;
  left:40px;
  margin-left: 10px;
  display: flex;
  align-items: center;
  max-width: 80%;
  max-height: 80%;
  justify-content: space-between;
}

.marked{
  text-decoration: line-through;
}

.clear-mark{
  text-decoration: none;
}
.delete {
  position: absolute;
  right:10px;
  background-color: red;
  border:none;
  border-radius: 5px;
  width: 25px;
  height: 25px;
  display: flex;
  padding:2px;
  align-items: center;
  justify-content: center;
  flex:1;

}
  

.add-form {
  margin-bottom: 40px;
}

.form-control {
  margin: 20px 0;
}

.form-control label {
  display: block;
}

.form-control input {
  width: 100%;
  height: 40px;
  margin: 5px;
  padding: 3px 7px;
  font-size: 17px;
}

.form-control-check {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.form-control-check label {
  flex: 1;
}

.form-control-check input {
  flex: 2;
  height: 20px;
}


